/*
FILESTRING.*:
(fileString: an object designed to homogenize the access to files,
windows resources and strings or memory in general)

These files contain the declaration of the fileString object,
which was designed to homogenize the access to files,
windows resources, strings and memory in general.

*/

#ifndef FILE_FILESTRING_INCLUDED
#define FILE_FILESTRING_INCLUDED

#define FILE_FILESTRING_VERSION "27-7-2003-H"// last modification: dd/mm/yy


#include "win-constants.h"

// FILE STRING CLASS
// The fileString object
class fileStringClass{
private:
	void* memory;
	bool shouldReleaseMemory;
	int size;
	bool ready;
public:
	// constructor / destructor
	fileStringClass():ready(false){}
	virtual ~fileStringClass(){close();}

	// openers
	virtual bool openFromFile(char* fileName);
/*	virtual bool openFromResource(char* resName, HINSTANCE hInstance);*/
	virtual bool openFromStringZ(char* stringz, bool localCopy=false);
	virtual bool openFromMemory(void* mem, int memSize, bool localCopy=false);

	// closers
	virtual bool close();

	// simple interfaces
	virtual void* getDataPointer();
	virtual bool isReady();
	virtual int getDataSize();
};// fileStringClass

#endif// FILE_FILESTRING_INCLUDED
